// src/components/FAQ.js
import "../App.css"; // namiesto "./App.css"
import { Link } from "react-router-dom";

const FAQ = () => {
  return (
    <section className="faq-section">
      <h1>asto kladen <span className="custom-gradient-text">Otzky</span></h1>

      <div className="faq-container">
        {/* Q1 */}
        <input type="radio" name="faq" id="q1" />
        <label htmlFor="q1">
          Koko u vs stoj tvorba webovej strnky? <span className="chevron"></span>
        </label>
        <div className="answer">
         <p>
  Cena je individulna a zvis od <strong>rozsahu projektu</strong>. 
  Pre presn cenov ponuku mi sta napsa vau predstavu a <strong>obratom vm vypracujem odhad na mieru</strong>.
</p>

        </div>

        {/* Q2 */}
        <input type="radio" name="faq" id="q2" />
        <label htmlFor="q2">
          Ako prebieha spoluprca? <span className="chevron"></span>
        </label>
        <div className="answer">
          <p>
            Najskr si prejdeme <strong>vae poiadavky</strong>, potom pripravm <strong>grafick nvrh</strong>.
            Po vaom schvlen sa pustm do <strong>kdovania a testovania</strong>, a km web nie je presne poda vaich predstv.
          </p>
        </div>

        {/* Q3 */}
        <input type="radio" name="faq" id="q3" />
        <label htmlFor="q3">
          Musm ma vlastn texty a fotky? <span className="chevron"></span>
        </label>
        <div className="answer">
          <p>
            Idelne no, pretoe <strong>vy poznte svoje podnikanie najlepie</strong>.
            Ak ich vak nemte, rd vm poradm, o by na strnke nemalo chba,
            alebo pomem s vberom <strong>vhodnch ilustranch obrzkov</strong>.
          </p>
        </div>

        {/* Q4 */}
        <input type="radio" name="faq" id="q4" />
        <label htmlFor="q4">
          Bude web sprvne zobrazen aj na mobiloch? <span className="chevron"></span>
        </label>
        <div className="answer">
          <p>
            Samozrejme. Vetky strnky, ktor tvorm, s <strong>plne responzvne</strong>.
            To znamen, e bud vyzera skvele na potai, tablete aj na mobilnom telefne.
          </p>
        </div>

        {/* Q5 */}
        <input type="radio" name="faq" id="q5" />
        <label htmlFor="q5">
          o ak budem chcie na strnke nieo zmeni neskr? <span className="chevron"></span>
        </label>
        <div className="answer">
          <p>
            Weby odovzdvam tak, aby ste si <strong>zkladn texty alebo fotky vedeli upravi aj sami</strong>.
            Taktie ponkam <a href="https://tvoj-web.sk/kontakt" target="_blank" rel="noopener noreferrer"><strong>dlhodob technick podporu</strong></a>,
            ak by ste potrebovali zloitejie pravy.
          </p>
        </div>

        {/* Tlaidlo pod FAQ */}
        <div className="faq-button-wrap">
          <Link to="/contact" className="faq-cta">
  Nezodpovedalo to vetko? Nap mi
</Link>
        </div>
      </div>
    </section>
  );
};

export default FAQ;
